<template>
    <section>
        <div id="map"></div>
        <div id="mapInfo">
            <ul>
                <li>
                    <a href="">Taro.Yamada</a> さんが、最寄を 神保町 に変更しました。 2019/05/02 18:32
                </li>
                <li>
                    <a href="">Chono</a> さんが、御茶ノ水 に 出張（2019/06/01 終日）を予定追加しました。2019/05/01 20:13
                </li>
                <li>
                    <a href="">Tommy</a> さんが、じゅうろくプラザ に OB会（2019/05/04 14:300）を予定追加しました。2019/04/24 13:32    
                </li>
            </ul>
        </div>
    </section>
</template>

<script>
export default {
    name : "Portal",
    
    data(){
        return {
            map         :   null,
            marker      :   [],
            infoWindow  :   []
        }
    },

    //  初期化
    mounted(){
        console.log("info:portal描画")

        // return

        var mapLatLng = new google.maps.LatLng({lat: this.$store.getters.portalMarkers[0].lat, lng: this.$store.getters.portalMarkers[0].lng});
        this.map = new google.maps.Map(document.getElementById('map'), {
            center: mapLatLng,
            zoom: 7
        });

        // マーカー毎の処理
        for (var i = 0; i < this.$store.getters.portalMarkers.length; i++) {
            var markerLatLng = new google.maps.LatLng({lat: this.$store.getters.portalMarkers[i].lat, lng: this.$store.getters.portalMarkers[i].lng}); // 緯度経度のデータ作成
            this.marker[i] = new google.maps.Marker({ // マーカーの追加
                position: markerLatLng, // マーカーを立てる位置を指定
                map: this.map // マーカーを立てる地図を指定
            });
    
            let profUrl = location.href + 'profile/' + this.$store.getters.portalMarkers[i].userNo

            this.infoWindow[i] = new google.maps.InfoWindow({ // 吹き出しの追加
                content: '<div class="mapInfo"><p class="markerTitle">' + this.$store.getters.portalMarkers[i].name + '</p><ul><li><a href="' +  profUrl + '">' + this.$store.getters.portalMarkers[i].userName +'</a> さんの最寄</li></ul></div>' // 吹き出しに表示する内容
            });
    
            this.markerEvent(i); // マーカーにクリックイベントを追加
        }
    },

    methods:{
        markerEvent(i) {
            var self = this
            this.marker[i].addListener('click', function() { // マーカーをクリックしたとき
                self.infoWindow[i].open(self.map, self.marker[i]); // 吹き出しの表示
            });
        }
    }
}
</script>

<style scoped>
/*--  Start of map section  --*/
    #map {
        height: 500px;
        width: 100%;
    }

    #mapInfo{
        padding: 5px 10px 10px 10px;
        display: block;
    }

    #mapInfo li{
        font-size: 90%;
    }

    /* .markerTitle{
        color : #00a7ce;
        font-size : 120%;
    } */
/*--  End of map section  --*/
</style>



